@import "./default/default.scss";

.news-block-swiper {
  background: rgba(249, 249, 249, 0.7);
  &__container {
    padding-top: 85px;
    padding-bottom: 90px;
    @media (max-width: $mobile) {
        padding-top: 30px;
        padding-bottom: 30px;
    }
    .swiper-news-block-swiper {
      overflow: hidden;
      &__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
      .swiper-wrapper.news-block-swiper__wrapper {
        margin-top: 66px;
        
        @media (max-width: $mobile) {
            margin-top: 15px;
        }
      }
      .header-news-block-swiper__title-block {
        .header-news-block-swiper__title {
          @include rale-800-45;
        }
        .header-news-block-swiper__description {
          @include rale-500-18;
          margin-top: 33px;
          @media (max-width: $mobile) {
            margin-top: 0;
          }
        }
      }
      .news-block-swiper__nav-buttons {
        margin-top: 10px;
        position: relative;
        display: flex;
        padding-left: 24px;
        padding-right: 24px;
        background-color: $darkGreen;
        width: 141px;
        height: 58px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        align-items: center;
        .swiper-button-next {
          position: relative;
          color: $white;
          width: 26px;
          height: 27px;
          margin: 0;
          padding: 0;
          top: 0;
          right: 0;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 18px;
            position: relative;
          }
        }
        .swiper-button-prev {
          position: relative;
          color: $white;
          width: 23px;
          height: 23px;
          margin: 0;
          padding: 0;
          top: 0;
          left: 0;
          &::after {
            content: "\e919";
            font-family: icomoon;
            font-size: 18px;
            transform: rotate(180deg);
            position: relative;
          }
        }
      }
      .swiper-slide.news-block-swiper__slide {
        background-color: $white;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: initial;
        img {
          width: 100%;
          height: 270px;
          object-fit: cover;
        }
        .slide-text-block {
          padding: 23px 23px 27px 23px;
          height: 100%;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          &__date {
            margin-top: 1px;
            @include mont-500-13;
            color: $gray;
          }
          &__title {
            @include rale-700-16;
            margin-top: 10px;
            max-width: 370px;
            line-height: 24px;
            @media (max-width: $mobile) {
              margin-top: inherit;
            }
          }
          &__description {
            @include rale-500-14;
            max-width: 350px;
            margin-top: 9px;
            @media (max-width: $mobile) {
              margin-top: inherit;
            }
          }
          &__text-button {
            display: block;
            margin-top: 20px;
            @media (max-width: $mobile) {
              margin-top: inherit;
            }
            &::after {
              content: "→";

              margin-left: 8px;
              font-size: 21px;
            }
            &:hover {
            }
            &:active {
            }
            &:focus {
            }
          }
        }
      }
    }
    .news-block-swiper__main-news-block-swiper.main-news-block-swiper {
    }
    .rectangle.unbackground {
      display: block;
      width: max-content;
      margin-left: auto;
      margin-right: auto;
      margin-top: 70px;
      padding-left: 29px;
      padding-right: 33px;
      border: 2px solid $darkGreen;
      @media (max-width: $mobile) {
        margin-top: 15px;
      }
      &:hover {
      }
      &:active {
      }
      &:focus {
      }
    }
  }
}
