@charset "UTF-8";
.custom-select {
  position: relative;
  font-family: "Montserrat";
}
.custom-select select {
  display: none;
}
.custom-select .select-selected {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 3px;
}
.custom-select .select-selected:hover {
  background: rgba(51, 125, 90, 0.1);
}
.custom-select .select-selected:after {
  font-family: icomoon;
  color: #93b474;
  font-size: 6px;
  content: "\e922";
  transform: rotate(180deg);
}
.custom-select .select-selected.select-arrow-active {
  border: 1.5px solid #337d5a;
}
.custom-select .select-selected.select-arrow-active:after {
  border-color: transparent transparent #93b474 transparent;
  transform: rotate(0deg);
}
.custom-select .select-items div,
.custom-select .select-selected {
  color: #1a2f3f;
  padding: 3px 0px 3px 5px;
  border: 1px solid transparent;
  border-color: transparent transparent rgba(0, 0, 0, 0.1) transparent;
  cursor: pointer;
  user-select: none;
  border: 0.5px solid rgba(0, 0, 0, 0.15);
}
.custom-select .select-items {
  position: absolute;
  background-color: #ffffff;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 99;
}
.custom-select .select-hide {
  display: none;
}
.custom-select .select-items .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}
.custom-select .select-items div:hover,
.custom-select .same-as-selected {
  background-color: #337d5a;
  color: #ffffff;
}

.news-block-swiper {
  background: rgba(249, 249, 249, 0.7);
}
.news-block-swiper__container {
  padding-top: 85px;
  padding-bottom: 90px;
}
@media (max-width: 600px) {
  .news-block-swiper__container {
    padding-top: 30px;
    padding-bottom: 30px;
  }
}
.news-block-swiper__container .swiper-news-block-swiper {
  overflow: hidden;
}
.news-block-swiper__container .swiper-news-block-swiper__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-wrapper.news-block-swiper__wrapper {
  margin-top: 66px;
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .swiper-wrapper.news-block-swiper__wrapper {
    margin-top: 15px;
  }
}
.news-block-swiper__container .swiper-news-block-swiper .header-news-block-swiper__title-block .header-news-block-swiper__title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-size: 45px;
  line-height: 45px;
  letter-spacing: 0.01em;
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .header-news-block-swiper__title-block .header-news-block-swiper__title {
    font-size: 25px;
    line-height: 25px;
  }
}
.news-block-swiper__container .swiper-news-block-swiper .header-news-block-swiper__title-block .header-news-block-swiper__description {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  letter-spacing: 0.01em;
  margin-top: 33px;
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .header-news-block-swiper__title-block .header-news-block-swiper__description {
    font-size: 14px;
  }
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .header-news-block-swiper__title-block .header-news-block-swiper__description {
    margin-top: 0;
  }
}
.news-block-swiper__container .swiper-news-block-swiper .news-block-swiper__nav-buttons {
  margin-top: 10px;
  position: relative;
  display: flex;
  padding-left: 24px;
  padding-right: 24px;
  background-color: #337d5a;
  width: 141px;
  height: 58px;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
}
.news-block-swiper__container .swiper-news-block-swiper .news-block-swiper__nav-buttons .swiper-button-next {
  position: relative;
  color: #ffffff;
  width: 26px;
  height: 27px;
  margin: 0;
  padding: 0;
  top: 0;
  right: 0;
}
.news-block-swiper__container .swiper-news-block-swiper .news-block-swiper__nav-buttons .swiper-button-next::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 18px;
  position: relative;
}
.news-block-swiper__container .swiper-news-block-swiper .news-block-swiper__nav-buttons .swiper-button-prev {
  position: relative;
  color: #ffffff;
  width: 23px;
  height: 23px;
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
}
.news-block-swiper__container .swiper-news-block-swiper .news-block-swiper__nav-buttons .swiper-button-prev::after {
  content: "\e919";
  font-family: icomoon;
  font-size: 18px;
  transform: rotate(180deg);
  position: relative;
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: initial;
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide img {
  width: 100%;
  height: 270px;
  object-fit: cover;
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block {
  padding: 23px 23px 27px 23px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__date {
  margin-top: 1px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 13px;
  letter-spacing: 0.01em;
  color: #595959;
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__title {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.01em;
  margin-top: 10px;
  max-width: 370px;
  line-height: 24px;
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__title {
    margin-top: inherit;
  }
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__description {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.01em;
  max-width: 350px;
  margin-top: 9px;
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__description {
    margin-top: inherit;
  }
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__text-button {
  display: block;
  margin-top: 20px;
}
@media (max-width: 600px) {
  .news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__text-button {
    margin-top: inherit;
  }
}
.news-block-swiper__container .swiper-news-block-swiper .swiper-slide.news-block-swiper__slide .slide-text-block__text-button::after {
  content: "→";
  margin-left: 8px;
  font-size: 21px;
}
.news-block-swiper__container .rectangle.unbackground {
  display: block;
  width: max-content;
  margin-left: auto;
  margin-right: auto;
  margin-top: 70px;
  padding-left: 29px;
  padding-right: 33px;
  border: 2px solid #337d5a;
}
@media (max-width: 600px) {
  .news-block-swiper__container .rectangle.unbackground {
    margin-top: 15px;
  }
}